@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Roboto', sans-serif;
}
.app {
  background:url(/bmw-tech-checklist/static/media/BMW-MY21-M4-Coupe-Gallery-01.2cf96d97.jpg);
  padding: 30px;
  height: 100vh;
}
.checklist-btn{
  text-transform: uppercase;
    padding: 0 5px 0 5px;
    width: 210px;
    height: 40px;
    border: 2px solid #11e011;
    font-size: 20px;
    font-weight: 500;
    font-style: normal;
    border-radius: 5px;
    line-height: 19px;
}
.todo-list {
  background: #e8e8e8;
  border-radius: 4px;
  padding: 5px;
  max-width: 600px;
}

.todo {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 3px 10px;
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo-input{
  height: 25px;
  width: 300px;
}
.title{
    color: #ffffff;
    font-weight: 500;
}
